.App {
  text-align: center;
  background-color: #1f2147;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 100px;
  pointer-events: none;
  margin: 10px;

}
.row-text {
  color: white;
}
.page-title {

  font-family: cursive;
}
.App-header {
  background-color: #EE6075;
  min-height: 35vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.footer {
  background-color: #1f2147;
  min-height: 15vh;
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
